*{
  margin: 0px;
  padding: 0px;
}
.App{
position: absolute;
top: 20%;
left: 40%;
height: 500px;
width: 350px;
background-color: rgb(85, 83, 83);
border-radius: 30px;
}
.header{
  margin: 20px 10px;
  border-radius: 17px;
  background-color: rgb(103, 159, 161);
  padding: 5px;
  height: 61px;
}
.header p{
font-size: 50px;
text-align: right;

}
.gird-container{
  display: grid;
  grid-template-columns: 70px 70px 70px 70px;
  grid-gap: 16px;
  padding: 10px;
}
.grid-item{
  padding: 10px 5px;
  font-size: 30px;
  text-align: center;
  border-radius: 10px;
}
.btnbg{
  background-color:sandybrown;
}